"use client";

import { useState } from "react";

const ProfileSection = () => {
  // Simulated user data (replace with real API data)
  const [user, setUser] = useState({
    name: "John Doe",
    email: "johndoe@example.com",
    role: "Student",
    profileImage: "/images/profile-placeholder.png", // Placeholder image
    coursesEnrolled: 5,
    coursesCompleted: 3,
    certifications: 2,
  });

  return (
    <div className="bg-white p-6 rounded-lg shadow-md w-full lg:w-1/4">
      {/* Profile Picture */}
      <div className="flex flex-col items-center">
        <img
          src={user.profileImage}
          alt="Profile"
          className="w-24 h-24 rounded-full shadow-lg mb-4"
        />
        <h2 className="text-xl font-bold">{user.name}</h2>
        <p className="text-gray-500">{user.email}</p>
        <span className="bg-blue-100 text-blue-700 px-3 py-1 rounded-full text-sm mt-2">
          {user.role}
        </span>
      </div>

      {/* Profile Stats */}
      <div className="mt-6">
        <p className="text-gray-600"><strong>Courses Enrolled:</strong> {user.coursesEnrolled}</p>
        <p className="text-gray-600"><strong>Courses Completed:</strong> {user.coursesCompleted}</p>
        <p className="text-gray-600"><strong>Certifications:</strong> {user.certifications}</p>
      </div>

      {/* Profile Actions */}
      <div className="mt-6 flex flex-col space-y-3">
        <button className="bg-blue-500 text-white py-2 px-4 rounded-lg hover:bg-blue-600">
          Edit Profile
        </button>
        <button className="bg-gray-300 py-2 px-4 rounded-lg hover:bg-gray-400">
          Change Password
        </button>
        <button className="bg-red-500 text-white py-2 px-4 rounded-lg hover:bg-red-600">
          Logout
        </button>
      </div>
    </div>
  );
};

export default ProfileSection;
