"use client";
import CourseCompleted from "@/components/CourseCompleted";
import CompletionGraph from "@/components/CompletionGraph";
import TimeSpent from "@/components/TimeSpent";
import Certifications from "@/components/Certifications";
import Layout from "@/components/Layout";
import ProfileSection from "@/components/ProfileSection"; // ✅ Import Profile Section

export default function DashboardPage() {
  return (
    <Layout>
      <div className="p-6 bg-gray-50 min-h-screen flex flex-col lg:flex-row gap-6">
        {/* Left Sidebar - Profile */}
        <aside className="w-full lg:w-1/4">
          <ProfileSection />
        </aside>

        {/* Main Content */}
        <main className="flex-1">
          <h1 className="text-3xl font-bold mb-6">Dashboard</h1>

          {/* Courses Completed */}
          <section className="mb-8">
            <CourseCompleted />
          </section>

          {/* Course Completion Graph */}
          <section className="mb-8">
            <CompletionGraph />
          </section>

          {/* Time Spent */}
          <section className="mb-8">
            <TimeSpent />
          </section>

          {/* Certifications */}
          <section className="mb-8">
            <Certifications />
          </section>
        </main>
      </div>
    </Layout>
  );
}
